.main-container {
    font-family: 'Blair ITC';
    display: grid;
    grid-template-columns: 0.85fr 2fr;
    margin-top: 1vw; }

.logo {
    font-size: 3vw;
    letter-spacing: 0.7vw; }

.subtitle {
    width: 45vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    letter-spacing: 0.4vw;
    font-family: 'Blair ITC Bold'; }

.logo-img {
    display: none; }

.text-container {
    display: grid;
    grid-template-columns: 0.85fr 2fr;
    margin-top: 3vw; }

.menu ul {
    font-size: 1.25vw;
    line-height: 3vw;
    padding-left: 1vw;
    letter-spacing: 0.25vw;
    margin: 0; }

.content {
    width: 45vw;
    font-size: 2vw;
    line-height: 2.5vw;
    letter-spacing: 0.05vw;
    height: 85vh;
    overflow-y: scroll; }

.content::-webkit-scrollbar {
    display: none; }


.content p {
    margin-top: 0; }

li a:hover {
    font-style: italic; }

.main-mobile {
        display: none; }

@media only screen and (max-width: 835px) {
    .main-container {
        display: none; }

    .main-mobile {
        display: block;
        display: flex;
        justify-content: space-between; }

    .logo, .subtitle {
        display: none; }

    .logo-img {
        display: block;
        margin-top: 2vh;
        margin-left: 4vw;
        width: 45vw; }

    .text-container {
        display: block; }

    .menu {
        display: none;
        margin-left: 4vw;
        margin-top: 2vh; }


    .menu ul {
        font-size: 4.5vw;
        line-height: 15vw; }

    .content {
        margin: 5vh 2vh;
        font-size: 5vw;
        width: 85vw;
        line-height: 7vw; }

    .close {
        display: none;
        font-family: 'Blair ITC';
        font-size: 5vw;
        padding: 2vw; } }
